import React, { Fragment, Component } from "react";
import "./App.css";

import Section from "./Components/Section/Section";

// Task1
import Feedback from "./Components/Feedback/Feedback";
import Statistics from "./Components/Statistics/Statistics";

class App extends Component {
  state = {
    good: 0,
    neutral: 0,
    bad: 0,
  };

  onLeaveFeedback = ({ target }) => {
    this.setState((state) => ({
      [target.name]: state[target.name] + 1,
    }));
  };
  countTotalFeedback = () => {
    const { good, neutral, bad } = this.state;
    this.total = good + neutral + bad;
  };
  countPositiveFeedbackPercentage = () => {
    this.positive = Math.round((100 / this.total) * this.state.good);
  };
  render = () => {
    this.countTotalFeedback();
    this.countPositiveFeedbackPercentage();
    const { good, neutral, bad } = this.state;

    return (
      <Fragment>
        <h1>Module 2</h1>
        <Section title="Please leave feedback">
          <Feedback options={Object.keys(this.state)} onLeaveFeedback={this.onLeaveFeedback} />
        </Section>
        <br />
        <Section title="Statistics">
          <Statistics good={good} neutral={neutral} bad={bad} total={this.total} positive={this.positive} />
        </Section>
      </Fragment>
    );
  };
}

export default App;
